<script>
    import { Accordion, AccordionItem, Alert, Label, Row, Col, Badge, ButtonGroup, Input, Icon, Tooltip } from "sveltestrap";
    import items from "./data/items.json";
    import enchantments from "./data/enchantments.json";

    let itemsAndEnchantments = [];

    let radioGroupValues = [];
    let radioGroupIncompatibles = [];

    prepareItemsAndEnchantments();

    function prepareItemsAndEnchantments()
    {
        Object.entries(items).forEach(itemPair => {
            const [itemKey, item] = itemPair;

            itemsAndEnchantments[itemKey] = [];
            radioGroupValues[itemKey] = [];
            radioGroupIncompatibles[itemKey] = [];

            Object.entries(enchantments).forEach(enchantmentPair => {
                const [enchantmentKey, enchantment] = enchantmentPair;

                if (enchantment.items.includes(itemKey)) {

                    itemsAndEnchantments[itemKey].push(enchantmentKey);
                    radioGroupValues[itemKey][enchantmentKey] = 0;
                    radioGroupIncompatibles[itemKey][enchantmentKey] = [];
                }
            });
        });
    }

    function chooseEnchantmentLevel(chosenItemKey, chosenEnchantmentKey, newValue)
    {
        if (newValue > 0)
        {
            // add incompatibilities
            enchantments[chosenEnchantmentKey].incompatibles.forEach(incompatibleEnchantmentKey => {
                radioGroupValues[chosenItemKey][incompatibleEnchantmentKey] = 0;
                if (radioGroupIncompatibles[chosenItemKey][incompatibleEnchantmentKey] != undefined && !radioGroupIncompatibles[chosenItemKey][incompatibleEnchantmentKey].includes(chosenEnchantmentKey))
                {
                    radioGroupIncompatibles[chosenItemKey][incompatibleEnchantmentKey].push(chosenEnchantmentKey);
                }
            });
        } else {
            // remove incompatibilities
            enchantments[chosenEnchantmentKey].incompatibles.forEach(incompatibleEnchantmentKey => {
                if (radioGroupIncompatibles[chosenItemKey][incompatibleEnchantmentKey] != undefined && radioGroupIncompatibles[chosenItemKey][incompatibleEnchantmentKey].includes(chosenEnchantmentKey))
                {
                    radioGroupIncompatibles[chosenItemKey][incompatibleEnchantmentKey].splice(radioGroupIncompatibles[chosenItemKey][incompatibleEnchantmentKey].indexOf(chosenEnchantmentKey), 1);
                }
            });
        }

        // trigger update, since push and splice does not trigger it
        radioGroupIncompatibles[chosenItemKey] = radioGroupIncompatibles[chosenItemKey];
    }

    function isIncompatible(incompatibles)
    {
        return incompatibles.length > 0;
    }

    function isActive(value, i)
    {
        return value == i;
    }
</script>

<h3>Level Calculator</h3>

<Alert color="warning">
    <b>Work in progress!</b><br />
    This section is currently not working.
</Alert>

<Accordion>
    {#each Object.entries(itemsAndEnchantments) as [itemKey, enchantmentKeys]}
        <AccordionItem header="{items[itemKey].name}" class="item-image item-image-{itemKey}">
            {#each enchantmentKeys as enchantmentKey}
                <Row>
                    <Label for="{itemKey}-{enchantmentKey}-control" sm=12 md=4 lg=3>
                        {#if isIncompatible(radioGroupIncompatibles[itemKey][enchantmentKey])}
                            <span class="text-muted position-relative" id="{itemKey}-{enchantmentKey}-incompatibles">
                                {enchantments[enchantmentKey].name}
                                <span class="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">
                                    {radioGroupIncompatibles[itemKey][enchantmentKey].length}
                                    <span class="visually-hidden">incompatible enchantments</span>
                                </span>
                            </span>
                            <Tooltip target="{itemKey}-{enchantmentKey}-incompatibles" placement="right">
                                <div class="text-start">
                                    Incompatible with:<br />
                                    {#each radioGroupIncompatibles[itemKey][enchantmentKey] as incompatibleEnchantmentKey}
                                        - {enchantments[incompatibleEnchantmentKey].name}<br />
                                    {/each}
                                </div>
                            </Tooltip>
                        {:else}
                            {enchantments[enchantmentKey].name}
                        {/if}
                    </Label>
                    <Col sm=12 md=4 lg=3>
                        <ButtonGroup>
                            {#each Array(enchantments[enchantmentKey].levelMax + 1) as _, i}
                                <Input type="radio" class="btn-check" bind:group={radioGroupValues[itemKey][enchantmentKey]} id="{itemKey}-{enchantmentKey}-{i}" value="{i}" on:change={e => chooseEnchantmentLevel(itemKey, enchantmentKey, e.target.value)} />    
                                <Label class="btn btn-outline-primary {isIncompatible(radioGroupIncompatibles[itemKey][enchantmentKey]) ? 'disabled' : ''} {isActive(radioGroupValues[itemKey][enchantmentKey], i) ? 'active' : ''} " for="{itemKey}-{enchantmentKey}-{i}">
                                    {#if i == 0}
                                        <Icon name="dash-circle" />
                                    {:else}
                                        {i}
                                    {/if}
                                </Label>
                            {/each}
                        </ButtonGroup>
                    </Col>
                </Row>
            {/each}
        </AccordionItem>
    {/each}
</Accordion>

<style>
