    :global(div.item-image-axe > h2 > button::before) { background-position: -144px -448px; }
    :global(div.item-image-trident > h2 > button::before) { background-position: -0px -336px; }
    :global(div.item-image-pickaxe > h2 > button::before) { background-position: -176px -448px; }
    :global(div.item-image-shovel > h2 > button::before) { background-position: -192px -448px; }
