    :global(div.item-image-crossbow > h2 > button::before) { background-position: -32px -368px; }
    :global(div.item-image-fishing_rod > h2 > button::before) { background-position: -224px -448px; }
    :global(div.item-image-shears > h2 > button::before) { background-position: -144px -464px; }
    :global(div.item-image-flint_and_steel > h2 > button::before) { background-position: -240px -448px; }
    :global(div.item-image-compass > h2 > button::before) { background-position: -32px -880px; }
    :global(div.item-image-carrot_on_a_stick > h2 > button::before) { background-position: -128px -448px; }
    :global(div.item-image-warped_fungus_on_a_stick > h2 > button::before) { background-position: -176px -816px; }
</style>